import React, { Component } from 'react';

import './App.css';

import { BrowserRouter, Switch, Route, Redirect } from "react-router-dom";

import ScrollTop from "./utils/scrollTop.js";

import NavBarArea from './layout-components/NavBarArea/NavBarArea.js';
import FooterArea from './layout-components/FooterArea/FooterArea.js';

import HomePage from './pages/HomePage/HomePage.js';
import DownloadPage from './pages/DownloadPage/DownloadPage.js';
import HelpPage from './pages/HelpPage/HelpPage.js';
import DonatePage from './pages/DonatePage/DonatePage.js';

class App extends Component {
  constructor() {
    super();
    this.state = {
      language: "en"
    };
    this.changeLanguage = (language) => {
      this.setState({language: language});
      window.history.replaceState(null, null, window.location.pathname + "?lang=" + language);
    }
  }

  render() {
    return (
      <div className="App">
        <BrowserRouter basename={process.env.PUBLIC_URL}>
          <ScrollTop/>
          <NavBarArea language={this.state.language} languageChange={this.changeLanguage}/>
          <main>
            <Switch>
              <Route exact path="/" render={() => <HomePage language={this.state.language} />} />
              <Route path="/download" render={() => <DownloadPage language={this.state.language} />} />
              <Route path="/help" render={() => <HelpPage language={this.state.language} />} />
              <Route path="/donate" render={() => <DonatePage language={this.state.language} />} />
              <Redirect to="/" />
            </Switch>
          </main>
          <FooterArea/>
        </BrowserRouter>
      </div>
    );
  }
}

export default App;