import React, { Component } from 'react';
import Nav from 'react-bootstrap/Nav';
import NavBar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import 'bootstrap/dist/css/bootstrap.min.css';

import './NavBarArea.css';

import { Link } from "react-router-dom";

import Icon from '../../components/Icon/Icon.js';


import logoImage from '../../img/logo.png';
import languageIcon from '../../img/language.png';


export default class NavBarArea extends Component {
    constructor(props) {
        super(props);
        var checkUrl = new URL(window.location.href);
        var lang = checkUrl.searchParams.get("lang");
        if (lang == "en") {
            this.props.languageChange("en");
        }
        else if (lang == "ko") {
            this.props.languageChange("ko");
        }
    }

    T(en, ko) {
        if (this.props.language == "en") {
          return en;
        }
        else {
          return ko;
        }
    }

    getUrl(path) {
        var checkUrl = new URL(window.location.href);
        var lang = checkUrl.searchParams.get("lang");
        if (lang == null) {
            return path;
        }
        else {
            return path + "?lang=" + this.props.language;
        }
    }

    render() {
        return (
            <div className="NavBar">
                <div className="nav-bar">
                    <NavBar collapseOnSelect fixed="top" bg="light" expand="lg">
                        <Link className="navbar-brand" onClick={() => window.location.href = this.getUrl("/")}>
                            <img className="nav-icon" src={logoImage} alt="TwitchLink" />
                            TwitchLink
                        </Link>
                        <NavBar.Toggle/>
                        <NavBar.Collapse id="basic-navbar-nav">
                            <Nav className="mr-auto">
                                <Nav.Item>
                                    <Nav.Link eventKey="1" as={Link} to={this.getUrl("/")}>
                                        TwitchLink
                                    </Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="2" as={Link} to={this.getUrl("/download")}>
                                        {this.T("Download", "다운로드")}
                                    </Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="3" as={Link} to={this.getUrl("/help")}>
                                        {this.T("Manual & Help", "사용법 & 도움말")}
                                    </Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="4" href="https://github.com/devhotteok/TwitchLink" target="_blank">
                                        {this.T("Support & Issues", "지원 & 버그 제보")}
                                    </Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="5" as={Link} to={this.getUrl("/donate")}>
                                        {this.T("Donate", "후원")}
                                    </Nav.Link>
                                </Nav.Item>
                            </Nav>
                            <Nav>
                                <NavDropdown alignRight title={
                                    <div style={{display: "flex"}}>
                                        <Icon size="small" src={languageIcon} alt="Language" style={{marginRight: "10px"}}/>
                                        Language / 언어
                                    </div>
                                }>
                                    <NavDropdown.Item onClick={() => this.props.languageChange("en")}>ENGLISH</NavDropdown.Item>
                                    <NavDropdown.Item onClick={() => this.props.languageChange("ko")}>한국어</NavDropdown.Item>
                                </NavDropdown>
                            </Nav>
                        </NavBar.Collapse>
                    </NavBar>
                    <div className="nav-bar-area"></div>
                </div>
            </div>
        );
    }
}